@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;900&display=swap);
*{padding:0;margin:0;box-sizing:border-box}html,body{width:100vw;height:100vh}body{margin:0;font-family:'Montserrat', sans-serif}code{font-family:'Montserrat', sans-serif}

.nav{width:100vw;display:flex;justify-content:space-around;align-items:center;background-color:#2d2e32;color:#fff;padding:1rem 0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2)}.nav .logo{font-weight:900;font-size:2.5rem;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;text-shadow:0 5px 5px rgba(0,0,0,0.2)}.nav .logo span{font-size:1.2rem;margin:0.2rem;font-weight:600}.nav nav ul{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;text-align:center;margin:0}.nav nav ul li{list-style:none;margin:0 2rem;font-weight:600;font-size:1.2rem;color:#b78c57;text-shadow:0 5px 5px rgba(0,0,0,0.2)}.nav nav ul .mail{background-color:#292a2e;width:50px;height:50px;border-radius:50%;display:flex;justify-content:center;align-items:center;box-shadow:0 5px 5px rgba(0,0,0,0.3);transition:all 0.3s ease-in-out;cursor:pointer}.nav nav ul .mail img{width:25px}.nav nav ul .mail:hover{box-shadow:none}@media (max-width: 800px){.nav{flex-direction:column;text-align:center}.nav nav{width:100vw}.nav nav ul{padding:0}.nav nav ul li{margin:0 0.5rem}.nav nav ul .mail{width:40px;height:40px}.nav nav ul .mail img{width:20px}}

.header{width:100vw;background-color:#2d2e32;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'text profile'}.header .text{padding:2rem;grid-area:text;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;text-align:left;margin:1rem}.header .text h3{color:#000;font-size:1.3rem;background-color:#64f4ab;display:inline-block;padding:0.5rem 1rem;border-radius:0.3rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);margin-right:7rem;transition:all 0.3s ease-in-out}.header .text h3:hover{box-shadow:none}.header .text h1{color:#fff;font-size:4rem}.header .text p{color:#999;font-size:1.3rem;font-weight:500;width:50%}.header .text a{color:#64f4ab;font-weight:600;background-color:#232427;box-shadow:0 10px 10px rgba(0,0,0,0.1);padding:0.5rem;font-size:1.3rem;border-radius:0.5rem;transition:all 0.3s ease-in-out;margin-right:15rem}.header .text a:hover{box-shadow:none;text-decoration:none}.profile{display:flex;justify-content:center;align-items:center;padding:2rem}.profile .image{background-image:linear-gradient(#d89b16, #7c5718);padding:1rem;border-radius:50%;margin-bottom:5rem;box-shadow:0 15px 15px rgba(0,0,0,0.2);transition:all 0.3s ease-in-out}.profile .image img{border-radius:50%;width:400px}.profile .image:hover{box-shadow:none}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 1050px){.header{grid-template-columns:100vw;grid-template-areas:'text' 'profile'}.header .text{padding:3rem}.header .text h3{font-size:1.1rem}.header .text h1{margin:1rem 0;font-size:3.5rem}.header .text p{margin:2rem 0;font-size:1.2rem;width:70%}.header .text a{margin:auto}.profile .image img{width:300px}}@media (max-width: 500px){.header .text{padding:3rem}.header .text h3{font-size:0.9rem;margin:auto}.header .text h1{margin:2rem auto;font-size:2rem}.header .text p{margin:1rem auto;font-size:1rem;width:90%}.header .text a{margin:auto}.profile .image img{width:200px}}

.about{background-color:#25262a;width:100vw;display:grid;grid-template-columns:40% 60%;grid-template-areas:'skills intro';padding:4rem 0 2rem}.about .skills{grid-area:skills;padding:2rem}.about .skills h3{margin:1rem;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.about .skills .skill{text-align:center;font-weight:500;color:#cda55f;background-color:#2d2e32;display:inline-block;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2);margin:1rem;padding:1rem 1rem 0.5rem;transition:all 0.3s ease-in-out}.about .skills .skill .stars{font-weight:900}.about .skills .skill:hover{box-shadow:none}.about .intro{grid-area:intro;padding:2rem}.about .intro h4{margin:2rem 0;color:#999;font-weight:600;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;text-shadow:0 5px 5px rgba(0,0,0,0.2)}.about .intro h1{margin:4rem 0;color:#fff;font-weight:700}.about .intro h1 span{margin:0 0 4rem;display:block;font-weight:500;color:#cda55f}.about .intro h3{color:#fff;font-size:1.5rem;margin:1rem 0 2rem}.about .intro p{width:80%;color:#999;font-size:1.2rem;font-weight:500;line-height:2.5rem}@media (max-width: 1100px){.about{text-align:center;grid-template-columns:100%;grid-template-areas:'skills' 'intro'}.about .intro h4{font-size:1.3rem}.about .intro h1{font-size:2rem}.about .intro h3{font-size:1.3rem}.about .intro p{font-size:1rem;width:100%}}

.works{width:100vw;background-color:#25262a;padding:4rem}.works h2{margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.works .project-container{display:flex;justify-content:space-around;flex-wrap:wrap}.works .project-container .project{cursor:pointer;margin:1rem;width:400px;background-color:#2d2e32;border-radius:1rem;padding:2rem;display:flex;justify-content:center;align-items:center;flex-direction:column;box-shadow:0 10px 10px rgba(0,0,0,0.1);transition:all 0.3s ease-in-out}.works .project-container .project .tools{color:#cda55f;display:flex;justify-content:space-evenly;flex-wrap:wrap}.works .project-container .project .tools div{margin:0.5rem;background-color:#25262a;padding:0.5rem;border-radius:0.5rem;box-shadow:0 5px 5px rgba(0,0,0,0.1)}.works .project-container .project:hover{box-shadow:10px 10px 10px rgba(0,0,0,0.3)}.works .project-container .project h3{margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.works .project-container .project img{width:350px;margin:1rem 0;border-radius:0.1rem;display:inline-block;box-shadow:0 10px 10px rgba(0,0,0,0.2);border-radius:0.5rem}.works button{background-color:#64f4ab;color:#000;padding:0.5rem 1rem;border-radius:0.5rem;font-size:1.1rem;border:none;box-shadow:0 10px 10px rgba(0,0,0,0.2);display:block;margin:4rem auto 2rem;transition:all 0.3s ease-in-out}.works button:hover{box-shadow:none;border:1px solid #64f4ab;color:#64f4ab;background-color:#25262a}@media (max-width: 500px){.works{padding:1rem}.works h2{font-size:1.5rem}.works .project-container .project{width:100%;padding:0.5rem}.works .project-container .project img{width:100%}.works .project-container .project h3{font-size:1.2rem}.works .project-container .project .tools div{font-size:0.9rem}}

.contact{background-color:#25262a;width:100vw;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'info social';border-top:1px solid #444;padding:4rem 0 1rem}.contact .info{text-align:center;grid-area:info;padding:1rem}.contact .info h3{font-size:1.5rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .info .email{color:#fff;font-weight:600}.contact .info .email a{color:#cda55f}.contact .info .footer{color:#fff}.contact .social{grid-area:social;text-align:center}.contact .social h3{font-size:1.5rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .social .social-icons{background-color:#cda55f;display:inline-block;padding:0.5rem;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2)}.contact .social .social-icons img{margin:0 1rem;width:30px}@media (max-width: 600px){.contact{padding:1rem;grid-template-columns:100%;grid-template-areas:'social' 'info'}.contact .info{margin:1rem 0}.contact .social{margin:1rem 0}.contact .social .social-icons{padding:0.5rem}.contact .social .social-icons img{width:20px;margin:0 0.5rem}}

